<template>
  <div class="wirte">
      <form v-on:submit="upload">
        <div class="lable">
          <label class="let">文章标题:</label>
          <input type="text" placeholder="请输入文章标题" v-model="blogs.title">
        </div>
        <div class="lable">
          <label class="let">文章作者:</label>
          <select v-model="blogs.author">
            <option  value="李磊">
              李磊
            </option>
            <option value="张三">
              张三
            </option>
            <option value="赵武">
              赵武
            </option>
          </select>
        </div>
        <div class="lable" >
          <label class="let">文章分类:</label> 
          <label class="let" ><input type="checkbox" v-model="blogs.class" value="VUE.js">VUE.js</label>
          <label class="let" ><input type="checkbox" v-model="blogs.class" value="NODE.js">NODE.js</label>
          <label class="let" ><input type="checkbox" v-model="blogs.class" value="ROUT.js">ROUT.js</label>
          <label class="let" ><input type="checkbox" v-model="blogs.class" value="ANGLAR.js">ANGLAR.js</label>
        </div>
        <div class="lable">
          <label class="let" style="vertical-align:top" >文章内容:</label>
          <textarea   rows="5" placeholder="请输入文章内容..." v-model="blogs.content"></textarea>
        </div>
        <div class="lable">
          <button type="submit">上传博客</button>
          <button v-on:click="cancel">取消编辑</button>
        </div>
      </form>
  </div>
</template>

<script>
export default {
  name: 'wirte',
  props: {
    msg: String
  },
  data(){
    return{
      blogs:{
        author:"李磊",
        class:["VUE.js"]
      }
    }
  },
  methods:{
    upload(e){
      if(this.blogs.title && this.blogs.content){
        this.$http.post("https://my-blog-48854.firebaseio.com/blogs.json",this.blogs).then(res=>{
        console.log(res)
        })
        e.preventDefault()
        let conf =confirm("发布博客成功，是否返回首页？")
        if(conf){
          this.$router.push({path:"/"})
        }
      }else{
        alert("请您在输入关键信息后再提交。")
      }
      e.preventDefault()
    },
    cancel(){
      this.$router.push({path:"/"})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.wirte{max-width: 1000px;margin:0 auto;padding: 15px;background: #fff;margin-top: 15px;}
.lable{margin-bottom:15px;}
.let{margin-right: 10px;display: inline-block;}
input[type=text]{border-radius: 4px;padding-left: 5px; height: 25px;border:solid 1px #a9a9a9; line-height: 25px;width: 90%;}
select{border-radius: 4px; height: 25px;line-height: 25px;min-width:80px;text-align: center;}
textarea{width: 90%;border-radius: 4px;line-height:25px ;font-size: 14px;padding:0 5px;}
button{width:80px;height: 30px;line-height: 30px;background:#cc0707 ;color:#fff;text-align: center;border:0;border-radius: 4px;cursor:pointer}
button[type=submit]{background:#169ad8;margin-right: 15px;}
</style>
