<template>
  <div class="home">
    <h1>博客总览</h1>
    <input type="text" placeholder="请输入您要搜索的内容..." v-model="soso">
    <div v-for="(item,k) in sosoUsers(blogs,soso)" v-bind:key="k" class="item">
      <div class="let">
        <div  class="title" >
        {{item.title | filterTitle}}
        </div>
        <div class="body">
          {{item.content | filterContent}}
        </div>
      </div>
      <div class="right">
        <router-link :to="'/editBlog/'+item.id"><button>编辑</button></router-link> 
        <button style="background:#cc0707;margin-left: 14px;" v-on:click="deleteBlog(item.id)">删除</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  props: {
    msg: String
  },
  data(){
    return{
      blogs:[],
      soso:""
    }
  },
  methods:{
    getBolgs(){
      this.$http.get("https://my-blog-48854.firebaseio.com/blogs.json").then(res=>{
        return res.json()
      }).then(res=>{
        var blogsArray=[];
        for(let key in res){
          res[key].id=key
          blogsArray.push(res[key])
        }
        this.blogs=blogsArray
      })
    },
    deleteBlog(id){
      this.$http.delete("https://my-blog-48854.firebaseio.com/blogs/"+id+".json").then(function(){
        alert("删除博客成功！")
        this.getBolgs()
      })
    },
     sosoUsers(blogs,soso){
      return blogs.filter(function(blog){
        return blog.title.match(soso)
      })
    }
  },
  computed:{
   
  },
  created(){
    this.getBolgs()
  }
}
</script>

